<template>
  <el-col :span='20'>
    <!--id-->
    <!-- ueditor.config.js-->
    <script :id="randomId" type="text/plain"></script>
      <div>
  <el-button @click='get_ueditor_data'></el-button>
  </div>
  <div v-html="ueditor_data['http_localhost_8080_test-drafts-data']"></div>
  <!--<div>{{ueditor_data['http_localhost_8080_test-drafts-data']}}</div>-->
  </el-col>

</template>

<script>

//  ueditor.config.js 
//var URL = window.UEDITOR_HOME_URL || '/static/ueditor_1/';

//
import '../../static/ueditor_1/ueditor.config.js'
import '../../static/ueditor_1/ueditor.all.min.js'
import '../../static/ueditor_1/lang/zh-cn/zh-cn.js'
//


export default {
  props: {},
  data() {
    return {
      //id,
      // randomId: 'editor_' + (Math.random() * 100000000000000000),
      randomId: 'test',
      //
      instance: null,
      ueditor_data:{}
    };
  },
  //--el,
  mounted() {
    this.initEditor()
  },

  beforeDestroy() {
    //  UEditor 
    if (this.instance !== null && this.instance.destroy) {
      this.instance.destroy();
    }
  },
  methods: {
    initEditor() {
      //dom
      this.$nextTick(() => {
        this.instance = UE.getEditor(this.randomId, this.ueditorConfig);
        //  UEditor  ready 
        this.instance.addListener('ready', () => {
          this.$emit('ready', this.instance);
        });
      });
    },
    get_ueditor_data(){
      this.ueditor_data =JSON.parse(localStorage.getItem('ueditor_preference'));
      console.log(this.ueditor_data );
      console.log(this.ueditor_data['http_localhost_8080_test-drafts-data'] );
      // this.ueditor_data = {'ada_asdadaqwewq-asd':"2"}
      // console.log(this.ueditor_data);
      // console.log(this.ueditor_data['ada_asdadaqwewq-asd']);

    }
  }
};
</script>



